@import url(https://fonts.googleapis.com/css?family=Raleway:300,400,500,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root
{
  --condiv-flex:6;
  --fontsize-p:1.3rem;
  --fontsize-nav-open:2rem;
  --typing-font:3rem;
  --icon-size:4rem;
  --navbar-width:2;
  --secs: 0.4s;
}
*
{
  margin:0;
  padding: 0;
}
body
{
  font-family: 'Raleway';
}
.App
{
  display: flex;
  width: 100%;
  min-height: 100vh;
  background-image: url(/portfolio/static/media/needles.2abe5141.jpeg);
  background-size: cover;
}
a:visited
{
  color: white;
}

.navigation
{
  position: absolute;
  font-size: 2rem;
  font-size: var(--fontsize-nav-open);
  
}
.navigation ul
{
  text-align: left;
  list-style-type: none;
  text-decoration: none;
}
.navigation li 
{
  margin: 0px 20px 30px 15px;
}
.navigation a:hover
{
  color: #d6d6d6;
}
.navigation a
{
  text-decoration: none;
  color: white;
}
.open 
{  
  min-height: 465px;
  min-width: 189.35px;
  background-color: #00000060;
  padding: 130px 0px 0px 0px;
  -webkit-transition: all 0.4s;
  transition: all 0.4s;
  -webkit-transition: all var(--secs);
  transition: all var(--secs);
  /*background: linear-gradient(180deg, rgb(32, 32, 32) 50%, rgb(90, 90, 90) 90%);*/
}
.closed 
{
  opacity: 0;
  padding: 120px 0px 0px 0px;
  -webkit-transform: translateX(-400%);
          transform: translateX(-400%);
  -webkit-transition: all 0.4s;
  transition: all 0.4s;
  -webkit-transition: all var(--secs);
  transition: all var(--secs);
}
.icon 
{ 
  padding: 10px 10px 10px 20px;
  width: 4rem;
  width: var(--icon-size); 
  height: 4rem; 
  height: var(--icon-size);
  line-height: 4rem;
  line-height: var(--icon-size); 
  font-size: 4rem; 
  font-size: var(--icon-size);
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none; 
  text-align: center;
  cursor: pointer;
  color: white;
  position: absolute;
  -webkit-transform: rotate(90deg);
          transform: rotate(90deg);
}
.icon:hover {
  background-color: #1a1a1ada;
  -webkit-transition: all 0.4s;
  transition: all 0.4s;
  -webkit-transition: all var(--secs);
  transition: all var(--secs);
}
.condiv
{
  padding: 15%;
  background-color: #00000086;
  flex:6 1;
  flex:var(--condiv-flex) 1;
  color: white;
}
.home
{
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
}
.profilepic
{
  border-radius: 50%;
  width: 250px;
  margin: 30px;
  border-width: thin;
  border-color: rgba(24, 24, 24, 0.596);
  border-style: solid;
}
.typingeffect
{
  font-size: 3rem;
  font-size: var(--typing-font);
  margin-bottom: 30px;
  color: white;
}
.subtopic
{
  margin:10px 10px 30px 10px !important;
}
p
{
  font-size: 1.3rem;
  font-size: var(--fontsize-p);
}
h1,h2,h3,h4,p
{
  margin: 10px;
}
.social
{
  margin-top: 20px;
  display: flex;
  text-align: center;
}
.social i
{
  padding: 0px 10px 0px 10px;
  font-size: 25px;
  color: #e6e6e6;
}
.social i:hover
{
  color: #ffffff;
}
.widecard
{
  border:0px solid #9a9b9c;
  display: flex;
  margin:30px 0px 30px 0px;
}
.widecard:hover
{
  color: #1b1d20;
  background-color: #ffffff;
  box-shadow: 0 10px 20px rgba(0,0,0,0.19), 0 6px 6px rgba(0,0,0,0.23);
}
.active
{
  font-weight: bolder;
}
.skills ul li
{
  list-style-type:none;
  margin:20px;
  font-size: 1.3rem;
  font-size: var(--fontsize-p);
}
